<template>
<view class="container">
<!-- 顶部输入区域 -->
<view class="search-area">
<view class="search-box">
<uni-icons v-if="searchText" type="search" size="16" color="#999"></uni-icons>
<input
v-model="searchText"
class="search-input"
type="text"
placeholder="请输入主题词"
placeholder-style="color: #999999"
/>
<uni-icons
v-if="searchText"
type="clear"
size="16"
color="#999"
@click="clearSearch"
></uni-icons>
</view>
</view>
<!-- 主内容区域 -->
<view class="content-area">
<!-- 左侧菜单 -->
<scroll-view class="left-menu" scroll-y>
<!-- 风格选择 -->
<view class="menu-section">
<view class="section-title">风格选择</view>
<view
v-for="(style, index) in styles"
:key="index"
:class="['menu-item', { active: currentStyle === style }]"
@click="selectStyle(style)"
>
{{ style }}
</view>
</view>
<!-- 字数设置 -->
<view class="menu-section">
<view class="section-title">字数设置</view>
<view
v-for="(count, index) in wordCounts"
:key="index"
:class="['menu-item', { active: currentWordCount === count }]"
@click="selectWordCount(count)"
>
{{ count }}字
</view>
</view>
</scroll-view>
<!-- 右侧预览区 -->
<view class="right-preview">
<view class="preview-header">预览效果</view>
<scroll-view class="preview-content" scroll-y>
<view class="preview-card">
{{ previewText }}
</view>
</scroll-view>
<view class="preview-footer">
<button class="copy-btn" @click="handleCopy">复制内容</button>
</view>
</view>
</view>
</view>
</template>
<script>
export default {
  data() {
    return {
      searchText: '',
      currentStyle: '正式商务',
      currentWordCount: '100',
      styles: [
        '正式商务',
        '轻松活泼',
        '文艺清新',
        '科技专业',
        '幽默诙谐',
        '严谨学术',
        '简约直接',
        '感性抒情'
      ],
      wordCounts: ['50', '100', '200', '300', '500', '1000']
    }
  },
  computed: {
    previewText() {
      if (!this.searchText) {
        return '请在上方输入主题词，系统将根据您选择的风格和字数生成相应的内容预览。';
      }
      return `基于主题"${this.searchText}"，采用${this.currentStyle}风格，生成${this.currentWordCount}字的文案内容。`;
    }
  },
  methods: {
    clearSearch() {
      this.searchText = '';
    },
    selectStyle(style) {
      this.currentStyle = style;
    },
    selectWordCount(count) {
      this.currentWordCount = count;
    },
    handleCopy() {
      uni.setClipboardData({
        data: this.previewText,
        success: () => {
          uni.showToast({
            title: '复制成功',
            icon: 'success',
            duration: 2000
          });
        }
      });
    }
  }
}
</script>
